.meterCard {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
}
.readingForm {
    flex: 1;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    border-radius: 10px;
    margin: 10px;
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
}
.widgetOptionsContainer {
    flex: 1
}

.widgetContainer {
    flex: 1;
    display: flex;
    justify-content: space-evenly;
}
.widget {
    position: relative;
    padding: 10px;
    // margin: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    border-radius: 10px;
    height: 100px;
}
.productSelector {
    position: absolute;
    right: 10px;
    color: green;
}

.inner-widget {
    display: flex;
    align-items: center;
}

.itemsSalesCloseContainer {
    padding-top: 5px;
    height: 160px !important;
    overflow-y: scroll;
   
   
}

.itemsForm {
    height: 568px !important;
}