.widget {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  flex: 1;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  height: 100px;

  //Designing the left and right section of each widget
  .left,
  .right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  //Styling the title
  .title {
    font-weight: bold;
    font-size: 14px;
    color: rgb(83, 83, 83);
  }

  //Styling the counter in widget
  .counter {
    font-size: 28px;
    font-weight: 300;
  }

  //Styling the link in widget
  .link {
    font-size: 12px;
    border-bottom: 1px solid gray;
    width: max-content;
  }

  //Styling Percentage item in widget
  .percentage {
    display: flex;
    align-items: center;
    font-size: 14px;

    //If percentage in positive
    &.positive {
      color: green;
    }
    // If the percentage is negative
    &.negative {
      color: red;
    }
  }

  //   Designing the Icons
  .icon {
    font-size: 18px;
    padding: 5px;

    border-radius: 5px;
    align-self: flex-end;
  }
}
